import React from 'react';
import PropTypes from 'prop-types'; 


const Timebox = props => {

    let label = props.label;

    if(props.value !== 1){
        label += 's'
    }

    return (
        <div>
            <p>{props.value} {label}</p>
        </div>
    )
}

Timebox.propTypes = {
    label: PropTypes.string,
    value: PropTypes.number
}

export default Timebox;