import React, { Component } from 'react';
import { Route, withRouter, Switch } from 'react-router-dom';
import Timer from '../../containers/Timer/Timer';
import ManageNames from '../../containers/ManageNames/ManageNames';
import Button from '../../UI/Button/Button';
import './ButtonMenus.css'
import { connect } from 'react-redux';



class ButtonMenus extends Component{

    homePage = () => {
        this.props.history.push("/how-long-does-vic-have/");
        this.props.changePath("/how-long-does-vic-have/");
    }

    countdownPage = () => {
        this.props.history.push("/how-long-does-vic-have/countdown");
        this.props.changePath("/how-long-does-vic-have/countdown");
    }

    manageNamesPage = () => {
        this.props.history.push("/how-long-does-vic-have/manage-names");
        this.props.changePath("/how-long-does-vic-have/manage-names");
    }


    componentDidMount = () => {
        this.props.changePath(this.props.location.pathname)
    };

    render(){
        const that = this
        const buttons = this.props.activePages.map(function(item){
            const func = that[item.function + 'Page'];

            if(that.props.path !== item.path){
                return (
                <li key={func} className="flexLi"> 
                    <Button 
                    click={func}
                    label={item.label}
                    /> 
                </li>
                );
            }

            return null
        });

        return(
        <div>
            <ul className="flexRow">
                {buttons}
            </ul>
            <Switch>
                <Route path="/how-long-does-vic-have/countdown" component={Timer} />
                <Route path="/how-long-does-vic-have/manage-names" component={ManageNames} />
                <Route path="/how-long-does-vic-have/" component="" />
            </Switch>
        </div>
        )
    }
}



const mapStateToProps = state => {
    return{
        activePages: state.activePages,
        path: state.pageActive
    };
};

const mapDispatchToProps = dispatch => {
    return{
        changePath: (value) => dispatch({type: 'CHANGE_PAGE', value: value})
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ButtonMenus));