import React, { Component } from 'react';
import './DeleteNamesModal.css'

class DeleteNamesModal extends Component{
    state = {
        modal_active: false
    }

    render(){

        const isDefault = (this.props.sys_id === 'default') ? true : false

        let modal = null;
        if(this.props.clicked === true && !isDefault){
            modal = (
                <div className='Modal'>
                    <h2>Are you sure you want to delete this record?</h2>
                    <p>{this.props.name}</p>
                    <div className='actionRow'>
                        <button onClick={this.props.btnCancelled} className ='button'>Cancel</button>
                        <div className='divider'/>
                        <button onClick={this.props.btnConfirm} className ='button'>Confirm</button>
                    </div>
                </div>
            )    
        }

        if(this.props.clicked === true && isDefault){
            modal = (
                <div key={this.props.key} className='Modal'>
                    <h2>Can't let you delete the default name</h2>
                    <p>{this.props.name}</p>
                    <div className='actionRow'>
                        <button onClick={this.props.btnCancelled} className ='button'>Cancel</button>
                        <div className='divider'/>
                        <button disabled className ='button'>Confirm</button>
                    </div>
                </div>
            )    
        }

        return(
            <div>
                {modal}
            </div>
        )
    }
}



export default (DeleteNamesModal)