import React, { Component } from 'react'
//const ms = require('pretty-ms')
import Msconverter from '../../components/msConverter/msConverter';
import PropTypes from 'prop-types'; 

class Timer extends Component{

    state = {
        timeNow: Date.now(),
        dateIwant: new Date('2020/06/04')
    }

    render(){

        return(
            <div>
                <p>You have this much time left...</p>
                <Msconverter
                timeNow={this.state.timeNow}
                dateIwant={this.state.dateIwant}
                />
            </div>
        );
        
    }
    componentDidMount() {
        this.interval = setInterval(() => this.setState({ timeNow: Date.now() }), 1000);
    }
    componentWillUnmount() {
        clearInterval(this.interval);
    }
}

Timer.propTypes = {
    timeNow: PropTypes.number,
    dateIwant: PropTypes.number
}

export default Timer;