import React from 'react';
import './OldTimer.css';
import minhand from './minhand.png';
import secondhand from './sechand.png';
import hourhand from './hourhand.png';
import PropTypes from 'prop-types'; 


const OldTimer = props => {

    let sdegree = props.time.seconds.value * 6;
    let hdegree = props.time.hours.value * 30 + (props.time.mins.value / 2);
    let mdegree = props.time.mins.value * 6;

    const srotate = "rotate(" + sdegree + "deg)"
    const hrotate = "rotate(" + hdegree + "deg)"
    const mrotate = "rotate(" + mdegree + "deg)";

    const sec = {
        'position': 'absolute',
        'width': '30px',
        'height': '600px',
        'top': '0px',
        'left': '285px',
        'background': "url(" + secondhand + ")",
        'zIndex': 3,
        'transform':  srotate
    };
        
    const min = {
        'position': 'absolute',
        'width': '30px',
        'height': '600px',
        'top': '0px',
        'left': '285px',
        'background': "url(" + minhand + ")",
        'zIndex': 2,
        'transform':  mrotate
    };
        
    const hour = {
        'position': 'absolute',
        'width': '30px',
        'height': '600px',
        'top': '0px',
        'left': '285px',
        'background': "url(" + hourhand + ")",
        'zIndex': 1,
        'transform':  hrotate
    };

    return(
        <div>
            <ul className='clock'>	
                    <li style={sec}></li>
                    <li style={hour}></li>
                    <li style={min}></li>
            </ul>
        </div>
    )
}

OldTimer.propTypes = {
    time: PropTypes.object
}

export default OldTimer;