import React from 'react';
import Timebox from "../Timebox/Timebox";
import OldTimer from '../OldTimer/OldTimer';


const Msconverter = props => {

    const time = {
        days: {
            value: 0,
            label: 'Day'
        },
        hours: {
            value: 0,
            label: 'Hour'
        },
        mins: {
            value: 0,
            label: 'Minute'
        },
        seconds: {
            value: 0,
            label: 'Second'
        }
    }


    const timeNow = props.timeNow;
    const dateIwant = props.dateIwant;

    const dateDiff = dateIwant - timeNow;

    let msLeft = '';

    let diffDay = dateDiff / 1000 / 60 / 60 / 24 ;
    diffDay = diffDay.toString().split(".");
    msLeft = '0.' + diffDay[1]

    let diffHours = (parseFloat(msLeft) * 24);
    diffHours = diffHours.toString().split('.');
    msLeft = '0.' + diffHours[1]

    let diffMin = (parseFloat(msLeft) * 60);
    diffMin = diffMin.toString().split('.');
    msLeft = '0.' + diffMin[1]

    let diffSeconds = (parseFloat(msLeft) * 60);
    diffSeconds = diffSeconds.toString().split('.');

    time.days.value = Math.floor(diffDay[0]);
    time.hours.value = Math.floor(diffHours[0]);
    time.mins.value = Math.floor(diffMin[0]);
    time.seconds.value = Math.floor(diffSeconds[0]);

    return(
        <div>
            {
                Object.values(time).map(k => {
                    return <Timebox
                    key = {k.value}
                    value = {k.value}
                    label = {k.label}
                    />
                })
            }

            <OldTimer
            time = {time}
            />

        </div>
    );

}

export default Msconverter;