import React, {Component} from 'react';
import { connect } from 'react-redux';
import './ManageNames.css';
import * as service from '../../API/API'
import DeleteNamesModal from '../DeleteNamesModal/DeleteNamesModal';

class ManageNames extends Component{

    state = {
        name: '',
        clickedModal: ''
    };

    changeNameSate = (e) => {
        this.setState({name: e.target.value});
    }

    sendNewName= () => {
        const name = [this.state.name];
        const nameString = this.state.name;
        let canUpdate = true
        for(let name in this.props.names){
            if(this.props.names[name].name === nameString){
                canUpdate = false;
            }
        }
        if(!canUpdate || nameString === ''){
            alert('NO CAN DO HOMIE');
        }else{
            service.sendData(name)
                .then(response => {
                    if(response.status === 200){
                        const newNames = [...this.props.names];
                        newNames.push({
                            sys_id: response.data.name,
                            name: nameString
                        });
                        this.props.updateNames(newNames);
                    }
            });
        }
    };

    deleteName = (index) => {
        const names = this.props.names;
        this.setState({clickedModal: names[index].sys_id})
    }

    confirmDelete = (index) => {
        const names = this.props.names;
        const newPropsNames = [...names];
        newPropsNames.splice(index, 1);

        service.deleteName(names[index]);
        this.props.updateNames(newPropsNames);
    }

    cancelDelete = () => {
        const blank = '';
        this.setState({clickedModal: blank})
    }

    shouldComponentUpdate = (nextProps, nextState) => {
        if(nextState.clickedModal !== this.state.clickedModal){
            return true;
        }
        if(this.props.names !== nextProps.names){
            return true
        }
        return false;
    }

    render(){

        const that = this;
        const list = this.props.names.map((name, index) => {
            const modalClicked =  (this.state.clickedModal !== name.sys_id) ? false : true;
            

            return (
                <li key={name.sys_id} className='nameLi'>
                    <div className='listItem' onClick={() => that.deleteName(index)}>
                        {name.name}
                    </div>
                    <DeleteNamesModal
                    name={name.name}
                    sys_id = {name.sys_id}
                    clicked={modalClicked}
                    btnConfirm = {() => that.confirmDelete(index)}
                    btnCancelled = {that.cancelDelete}/>
                </li>
            )
        });

        const classMain = [];
        classMain.push('inputArea');
        //if(this.state.clickedModal !== '') classMain.push('blur');

        return(
            <div className={classMain.join(' ')}>
                <h2>Please enter a new name</h2>
                    <input className="input" onChange={this.changeNameSate}></input>
                    <input
                        className="inputButton"
                        type="button"
                        value="Create Name"
                        onClick={this.sendNewName}
                    />
                    <ul className='nameList'>
                        {list}
                    </ul>
            </div>
        )
    }

}




const mapStateToProps = state => {
    return{
        names: state.names
    };
};

const mapDispatchToProps = dispatch => {
    return{
        updateNames: (value) => dispatch({type: 'UPDATE_NAMES', value: value})
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ManageNames);