import axios from '../axios';


export function sendData(name){

    return axios.post('/names.json', name)
        .then(response => {return response})
        .catch(error => console.log(error));

}


export function getNames() {
    return axios.get("https://vic-app-df7a3.firebaseio.com/names.json")
            .then(response => { return response})

}

export function deleteName(name){

    return axios.delete('/names/' + name.sys_id + '.json')
        .then(response => {return response})
        .catch(error => console.log(error));

}